.admin-page {
  padding: 30px;
}

.admin-table {
  width: 100%;
  border-collapse: collapse;
}

.admin-table th,
.admin-table td {
  padding: 10px;
  border-bottom: 1px solid #ddd;
}

.status.approved { color: green; font-weight: bold; }
.status.rejected { color: red; font-weight: bold; }
.status.pending { color: orange; font-weight: bold; }

/* ===== MODAL ===== */
.modal-overlay {
  position: fixed;
  inset: 0;
  background: rgba(0,0,0,.5);
  display: flex;
  justify-content: center;
  align-items: center;
}

.modal-content {
  background: white;
  padding: 30px;
  max-width: 800px;
  width: 100%;
  position: relative;
}

/* ===== WATERMARK ===== */
.watermark {
  position: absolute;
  inset: 0;
  display: flex;
  justify-content: center;
  align-items: center;
  opacity: 0.08;
  pointer-events: none;
}

.watermark img {
  width: 300px;
}

/* ===== ACTIONS ===== */
.modal-actions {
  display: flex;
  gap: 10px;
  margin-top: 20px;
}

.modal-actions button {
  padding: 10px 15px;
  border: none;
  cursor: pointer;
}

.approve { background: green; color: white; }
.reject { background: red; color: white; }
.print { background: #0d6efd; color: white; }

/* ===== PRINT MODE ===== */
@media print {
  body * {
    visibility: hidden;
  }

  .printable,
  .printable * {
    visibility: visible;
  }

  .printable {
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
  }

  .no-print {
    display: none;
  }
}
.status.approved { color: green; font-weight: bold; }
.status.rejected { color: red; font-weight: bold; }
.status.pending { color: orange; font-weight: bold; }

.locked-badge {
  color: #198754;
  font-weight: 700;
  margin-top: 10px;
}

.convert { background: #6f42c1; color: #fff; }

@media print {
  .no-print { display: none; }
}
.admission-letter {
  page-break-after: always;
}