/* ===============================
   GENERAL PAGE LAYOUT
================================ */
.page-container {
    max-width: 1100px;
    margin: auto;
    padding: 20px;
}

/* ===============================
   NEWS PAGES
================================ */
.news-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 20px;
}

.news-card {
    background: #fff;
    border-radius: 10px;
    padding: 15px;
    box-shadow: 0 2px 8px rgba(0, 0, 0, 0.08);
}

.news-card img {
    width: 100%;
    height: 160px;
    object-fit: cover;
    border-radius: 8px;
}

.news-details img {
    width: 100%;
    border-radius: 10px;
    margin-bottom: 15px;
}

/* ===============================
   ADMIN HOME CONTENT
================================ */
.admin-list-item {
    display: flex;
    justify-content: space-between;
    padding: 10px;
    background: #f8f9fa;
    border-radius: 6px;
    margin-bottom: 8px;
}

/* ===============================
   STUDENT PROFILE
================================ */
.profile-card {
    background: #fff;
    border-radius: 10px;
    padding: 20px;
}

.profile-tabs {
    display: flex;
    gap: 10px;
    margin-bottom: 20px;
}

.profile-tabs button {
    background: #e9ecef;
    color: #333;
}

.profile-tabs button.active {
    background: var(--primary-blue-dark);
    color: #fff;
}

/* ===============================
   RESULTS (PRINT VIEW)
================================ */
.result-sheet {
    background: #fff;
    padding: 30px;
}

.result-header {
    display: flex;
    gap: 15px;
    align-items: center;
}

.result-footer {
    display: flex;
    justify-content: space-between;
    margin-top: 30px;
}

.watermark {
    position: fixed;
    opacity: 0.06;
    width: 60%;
    top: 25%;
    left: 20%;
    z-index: -1;
}

@media print {
    button {
        display: none;
    }
}/* ===============================
   HOMEPAGE  LATEST NEWS
================================ */
.home-page .news {
    background: #f8f9fb;
    padding: 40px 20px;
}

.home-page .news h2 {
    text-align: center;
    margin-bottom: 25px;
}

.home-page .news-grid {
    display: grid;
    grid-template-columns: repeat(auto-fit, minmax(260px, 1fr));
    gap: 20px;
}

.home-page .news-card {
    background: #ffffff;
    border-radius: 12px;
    padding: 15px;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.08);
    transition: transform 0.2s ease, box-shadow 0.2s ease;
}

.home-page .news-card:hover {
    transform: translateY(-4px);
    box-shadow: 0 8px 18px rgba(0, 0, 0, 0.12);
}

.home-page .news-card img {
    width: 100%;
    height: 160px;
    object-fit: cover;
    border-radius: 8px;
    margin-bottom: 10px;
}

.home-page .news-card h3 {
    font-size: 16px;
    margin: 5px 0;
    color: #222;
}

.home-page .news-card small {
    color: #777;
    font-size: 12px;
}

.home-page .news-card p {
    font-size: 14px;
    margin: 8px 0;
    color: #444;
}

.home-page .news-card strong {
    color: var(--primary-blue-dark);
    font-size: 14px;
}@media print {
  .page-break {
    page-break-after: always;
  }
}
